import BigNumber from 'bignumber.js'
import NftAbi from "../nftAbi.json";
import React from 'react';
import './App.css';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import { Modal } from 'react-responsive-modal';
import Loader from 'react-loader-spinner';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import {NftContract} from "../constants/index";

class NFTEarly extends React.Component <any, any> {

    constructor(props) {
        super(props)
        this.state = {
          account: '',
          loading: false,
          networkId: 0,
          balanceOf:0,
          pass:"NFTS",
          nbNFT: new BigNumber(0),
          claimedPanties:0,
          totalMINT:0,
          mintPrice: 0,
          maxPanties:5000,
          totalSupply: 0,
          tokenRewardsClaimable:0,
          publicActive: false,
          privateActive:false,
          MintActivated: false,
          transaction:'',
        }
      }


  networkId = 1;
  bigNumber =   "1000000000000000000";
  NftContract = NftContract;
  NftApp = null;

  async checkpublicActiveTimestamp(){
    let publicActive = await this.NftApp.methods.MintEarlyEnd().call() * 1000
    let Active = await this.NftApp.methods.MintActivated().call()
    let dd = new Date()
    let now = Date.parse(dd.toString())
    if ( (now > publicActive) && Active){
        this.setState({publicActive:true})
        this.setState({privateActive:false})
    }
    else if ( now < publicActive && Active ){
        console.log("PRIVA")
        this.setState({privateActive:true})
        this.setState({publicActive:false})
        }
    this.setState({Active:Active})
    console.log(this.state.publicActive,Active,now,this.state.privateActive)
  }


  async componentWillMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidUpdate(prevProps) {
      if(this.props.account !== this.state.account && this.props.web3 !== null)
        await this.loadContract()
    }

 async loadContract() {
    this.NftApp = new this.props.web3.eth.Contract(NftAbi, this.NftContract);
    await this.connect()
    await this.checkpublicActiveTimestamp()
  }

  async connect() {

    //console.log(this.props.account)
    if(this.props.account){
        this.setState({ account: this.props.account })
        this.setState({
          totalMINT:  Number(await this.NftApp.methods.balanceOf(this.props.account).call()),
          MintActivated:  (await this.NftApp.methods.MintActivated().call()),
          totalSupply:  Number((await this.NftApp.methods.totalSupply().call())),
          mintPrice: Number(await this.NftApp.methods.mintPrice().call()),
          maxPanties: Number(await this.NftApp.methods.maxEarly().call()),
          nbNFT: new BigNumber(10)
        })
    }
  }


 async updateNFT(){
        let value = document.getElementById("walletToCheck")  as HTMLInputElement ;
        if (new BigNumber(value.value).eq(new BigNumber(1))){
            this.setState({pass:"NFT"})
            this.setState({nbNFT:new BigNumber(1)})
        }
        else if (new BigNumber(value.value).gt(new BigNumber(1)) && new BigNumber(value.value).lte(new BigNumber(10))){
            this.setState({pass:"NFTs"})
            this.setState({nbNFT:new BigNumber(value.value)})
        }
        else if (new BigNumber(value.value).gt(new BigNumber(10))){
            this.setState({pass:"NFTs"})
            this.setState({nbNFT:new BigNumber(10)})
        }
        else{
            this.setState({pass:"NFTs"})
            this.setState({nbNFT:new BigNumber(10)})
        }
     }


  resolve(transactionHash){
   this.setState({loading:true})
   //console.log(transactionHash,"transactionHash")
   this.setState({transaction:"https://rinkeby.etherscan.io/tx/"+transactionHash})
  }

  reject(err){
   this.setState({loading:false}); 
   //console.log(err)
  }

  loaderModal(receipt){
    this.setState({loading:false}); 
    //console.log(receipt['transactionHash'])
    this.connect()
  }
    

  closeModal(){
    this.setState({loading:false}); 
  }

 async claimToken(){

    let bigvalue = this.state.nbNFT
    let amountETH = new BigNumber(bigvalue).times(this.state.mintPrice)


let proof = [
  '0xa5dc538ec76deabd1f1a4022d8a92641966c7f916b489b445462522f0ae3b3a8'
]

    await new Promise((resolve, reject) => {
       this.NftApp.methods.earlyMint(bigvalue.toNumber(),0,proof)
      .send({from: this.state.account, value: amountETH})
        .once('transactionHash', (transactionHash) => {
            this.resolve(transactionHash)
        })
        .on('confirmation', (confirmationNumber) => {
            //this.resolve(confirmationNumber)
        })
        .on('error', err => this.reject(err))
        .then( receipt =>
            this.loaderModal(receipt)
        );
    })
  }

    render() {
     if(!this.props.web3){
        return(
            <AppWrapper>
            <ConnectWalletWrapper>
               <Wrapper>
                 <Title>
                 "Unlock Your Metamask or click Connect"
                  </Title>
               </Wrapper>
            </ConnectWalletWrapper>
            </AppWrapper>
        )
      }
      else
    {
    return (
      <AppWrapper>
                <Modal 
                    classNames={{
                      overlay: 'customOverlay',
                      modal: 'customModal',
                      overlayAnimationIn: 'customEnterOverlayAnimation',
                      overlayAnimationOut: 'customLeaveOverlayAnimation',
                      modalAnimationIn: 'customEnterModalAnimation',
                      modalAnimationOut: 'customLeaveModalAnimation',
                    }}
                    open={this.state.loading} 
                    animationDuration={800}
                    center 
                    onClose={() => this.closeModal()} 
                    >
                        <p>Waiting transaction to be approved</p>
                        <Loader type="Puff" color="#00BFFF" height="100" width="100" />
                        <a href={this.state.transaction}  rel="noreferrer" target="_blank">EtherScan transaction</a>
                </Modal>

        {this.state.MintActivated  && this.state.privateActive && !this.state.publicActive &&
            <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    "{this.state.totalSupply}/{this.state.maxPanties} total claimed."
                </Title>
                <Spacer/>
                  <Title>
                    "Buy your DirtyPanties NFT purchase up to 10."
                  </Title>
                  <Spacer/>
              </Wrapper>
              <MintInput onChange={() => this.updateNFT()} id="walletToCheck" placeholder={this.state.nbNFT} />
              <Spacer/>
              <ButtonClaim onClick={this.state.account === '' ? '' : () => this.claimToken()}>{this.state.account === '' ? 'Not connected' : `Claim ${this.state.nbNFT} ${this.state.pass}`}</ButtonClaim>
              <Spacer/>
            </ConnectWalletWrapper>
        }

        {!this.state.MintActivated  && !this.state.privateActive && !this.state.publicActive &&

           <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    "Early Access is not opened yet."
                </Title>
                </Wrapper>
            </ConnectWalletWrapper>

        }

        {this.state.MintActivated  && !this.state.privateActive && this.state.publicActive &&

           <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    "Early Access is closed You missed it."
                </Title>
                </Wrapper>
            </ConnectWalletWrapper>

        }

    {this.state.MintActivated  && this.state.privateActive && !this.state.publicActive &&
    <ContainerRewardsW>
      <Glow></Glow>
      <RewardsWrapper>
          <ContainerPanels>
            <ContainerPaidHolders>
                <div>Panties NFT</div>
                <div><span>{this.state.totalMINT}</span> </div>
            </ContainerPaidHolders>
            </ContainerPanels>
          </RewardsWrapper>
        </ContainerRewardsW>
        }
      </AppWrapper>
    );
    }
  }
}

const Glow = styled.div`
position: absolute;
top: 60%;
left: 10%;
z-index: -3;
box-shadow: 110px 50px 130px 60px #fcd63594;

  @media only screen and (min-width: 768px) {
    box-shadow: 150px 70px 170px 70px #fcd63594;
  }
`

const AppWrapper = styled.div`
    display: flex;
    flex-direction: column;
    min-height: 10vh;

`

const Spacer = styled.div`
flex: 1;
`

const ContainerPanels = styled.div`
  position:relative;
  margin-top: 5%;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  text-align: center;

  @media only screen and (min-width: 768px) {
    margin-top: 1px;
  }
`

const RewardsWrapper = styled.div`
  padding: 50px 25px 25px 25px;

  @media only screen and (min-width: 768px) {
    padding: 100px 133px 82px 133px;
  }
`

const ContainerRewardsW = styled.div`
  position: relative;
  width: 90%;
  margin: 10px auto 0;

  @media only screen and (min-width: 768px) {
    width: 950px;
  }
`

const MintInput = styled.input`
  border-color: #fff;
  background-color: transparent;
  padding: 2px 5px;
  color: #fff;
  font-size: 2em;
  text-align: center;
  border: 0px solid;
  border-radius: 7px;
  max-width: 12%;
  outline: 0;
  font-style: normal;
  font-weight: 400;
  line-height: 17px;

  width: 60%;

  @media only screen and (min-width: 768px) {
    padding: 2px 55px;
    width: 60%;
  }
`

const ContainerPaidHolders = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 80px;
  width: 80px;

  color: white;
  border: 1px solid #D29EE9;
  background: rgb(252 214 53 / 20%);
  border-radius: 7px;
  font-family: Red Hat Text;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  color: white;
  background: #AD1DEB12;
  padding: 5px 1px;

  background-color: transparent;
  letter-spacing: 1px;
  text-transform: uppercase;

  & span {
    color: #fcd635;
  }

  @media only screen and (min-width: 768px) {
    width: unset;
    padding: 20px 15px;
    height: 120px;
    font-size: 14px;
    width: 180px;
    letter-spacing: 2px;
  }
`

const ConnectWalletWrapper = styled.div`
  margin-top: 25px;
  text-align: center;
`

const ButtonClaim = styled.button`
  border: 2px solid #D29EE9;
  border-radius: 7px;
  font-family: 'Red Hat Text';
  font-size: 8px;
  font-style: normal;
  font-weight: 700;
  line-height: 5px;
  color: white;
  padding: 8px  14px;
  background-color: transparent;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    border: 3px solid #D29EE9;
    outline: 5px solid #fff;
    outline-offset: -5px;
  }

  @media (min-width: 768px) {
    border: 3px solid #D29EE9;
    font-size: 10px;
    padding-left: 2px;
    padding-right: 2px;
    line-height: 19px;
    padding: 10px 18px;
  }
`

const Title = styled.text`
  font-size: 1.2em;
  text-align: center;
  #color: #fcd63594;
  color: white;
  font-size: 11px;
  font-weight: 700;

  background-color: transparent;
  letter-spacing: 1px;
  text-transform: uppercase;

  @media (min-width: 768px) {
    letter-spacing: 1px;
    font-size: 18px;
    padding-left: 2px;
    padding-right: 2px;
  }
`;

const Wrapper = styled.div`
  padding: 1em;
`;


export default NFTEarly;
