import NftAbi from "../nftAbi.json";
import React from 'react';
import './App.css';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import {NftContract, display } from "../constants/index";
import axios from 'axios';

interface WalletItemProps{
    tokenId?: number,
    name?: string,
    description?: string,
    attributes?: [],
    image?: string
}

const WalletItem: React.FC<WalletItemProps> = ({tokenId, name, description, attributes, image}) => {

    return (
        <GridItem href={`/#/dirtypanties/${tokenId}`}>
            <ItemImagecontainer>
                <Container>
                <ItemImageInnercontainer>
                    <ImgWrapper src={image} height={`47px`}/>
                </ItemImageInnercontainer>
                </Container>
                <ItemValueContainer>
                    <StyledItemValue>{name}</StyledItemValue>
                </ItemValueContainer>
            </ItemImagecontainer>
        </GridItem>
    )
}

class Wallet extends React.Component <any, any> {

    constructor(props) {
        super(props)
        this.state = {
          account: '',
          balancePanties:0,
          loading: false,
          networkId: 0,
          nfts: [],
          page: 1
        }
      }


  NftContract = NftContract;
  NftApp = null;

  async componentWillMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async changeProps(value){
    this.setState({'page': value})
    this.refresh_stake(value)
  }

  async componentDidMount() {
    
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidUpdate(prevProps) {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
        await this.loadContract()
  }

  async loadContract() {
    this.NftApp = new this.props.web3.eth.Contract(NftAbi, this.NftContract);
    await this.connect()
  }


 refresh_stake(page) {
        let furl = 'https://testnet.dirtypanties.xyz/api/v1/nftfilter/'+ this.state.account + '/' + page;
        axios({
          url: furl,
          method: 'get'
           }).then((response) => {
                console.log(response.data);
                this.setState({nfts: response.data});
           });
 }

  async connect() {

    if(this.props.account){
        this.setState({ account: this.props.account })
        this.setState({
          balancePanties: await this.NftApp.methods.balanceOf(this.props.account).call()
        })
        this.refresh_stake(this.state.page)
    }
  }


  render() {
  if(!this.props.web3){
    return(
        <AppWrapper>
        <ConnectWalletWrapper>
           <Wrapper>
             <Title>
             "Unlock Your Metamask or click Connect."
              </Title>
           </Wrapper>
        </ConnectWalletWrapper>
        </AppWrapper>
    )
  }

  else if (Number(this.state.balancePanties) === 0) {

    return (
            <AppWrapper>
            <ConnectWalletWrapper>
                <Wrapper>
                    <Title>
                        "You don't own any DirtyPanties NFT."
                    </Title>
               </Wrapper>
            </ConnectWalletWrapper>
            </AppWrapper>
    )
  }
  else
  {
    return (
    <ScrollableContainer>
      <GridContainer>
        {this.state.nfts.length>0 && this.state.nfts.map((item) => (<WalletItem key={item.tokenId} tokenId={item.tokenId} name={item.name} image={item.thumbnail} description={item.description} attributes={item.attributes}/>))}
      </GridContainer>
      <ConnectWrapper>
       {this.state.balancePanties > Number(display) &&
       [...Array(Math.ceil(this.state.balancePanties/Number(display)))].map((x, i) =>
         <ConnectButton onClick={() => this.changeProps(i+1)} id="page" value={i+1}  key={i+1} > {i+1}</ConnectButton>
       )
       }
       </ConnectWrapper>
      </ScrollableContainer>
    );
    }
  }
}



