import BigNumber from 'bignumber.js'
import TokenAbi from "../tokenAbi.json";
import NftAbi from "../nftAbi.json";
import React from 'react';
import './App.css';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import { Modal } from 'react-responsive-modal';
import Loader from 'react-loader-spinner';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import {NftContract, TokenContract} from "../constants/index";

class TOKEN extends React.Component <any, any> {

    constructor(props) {
        super(props)
        this.state = {
          account: '',
          loading: false,
          networkId: 0,
          tokenRewardsClaimable:0,
          tokenRewards:0,
          transaction:'',
        }
      }


  balancePanties = 0;
  networkId = 1;
  bigNumber =   "1000000000000000000";
  TokenContract = TokenContract;
  NftContract = NftContract;
  TokenApp = null;
  NftApp = null;

  async componentWillMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidUpdate(prevProps) {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      {
        //console.log("componentDidUpdate")
        await this.loadContract()
      }
    }

  async loadContract() {
    this.TokenApp = new this.props.web3.eth.Contract(TokenAbi, this.TokenContract);
    this.NftApp = new this.props.web3.eth.Contract(NftAbi, this.NftContract);
    await this.connect()
  }

  async connect() {

    //console.log(this.props.account)
    if(this.props.account){
        this.setState({ account: this.props.account })
        this.setState({
          tokenRewards: new BigNumber(await this.TokenApp.methods.balanceOf(this.props.account).call()).div(10**18).toFixed(2)
        })
        this.setState({
          tokenRewardsClaimable: new BigNumber(await this.TokenApp.methods.getTotalClaimable(this.props.account).call()).div(10**18).toFixed(2)
        })
    }
  }

  resolve(transactionHash){
   this.setState({loading:true})
   //console.log(transactionHash,"transactionHash")
   this.setState({transaction:"https://rinkeby.etherscan.io/tx/"+transactionHash})
  }

  reject(err){
   this.setState({loading:false}); 
   //console.log(err)
  }

  loaderModal(receipt){
    this.setState({loading:false}); 
    //console.log(receipt['transactionHash'])
    this.connect()
  }
    

  closeModal(){
    this.setState({loading:false}); 
  }

  
  async claimToken(){
    await new Promise((resolve, reject) => {
      this.NftApp.methods.getReward()
      .send({from: this.state.account })
        .once('transactionHash', (transactionHash) => {
            this.resolve(transactionHash)
        })
        .on('confirmation', (confirmationNumber) => {
            //this.resolve(confirmationNumber)
        })
        .on('error', err => this.reject(err))
        .then( receipt =>
            this.loaderModal(receipt)
        );
    })
  }


  render() {
  if(!this.props.web3){
    return(
    <AppWrapper>
        <ConnectWalletWrapper>
           <Wrapper>
             <Title>
             "Unlock Your Metamask or click Connect"
              </Title>
           </Wrapper>
        </ConnectWalletWrapper>
        </AppWrapper>
    )
  }
  else
  {
    return (
      <AppWrapper>
                <Modal 
                    classNames={{
                      overlay: 'customOverlay',
                      modal: 'customModal',
                      overlayAnimationIn: 'customEnterOverlayAnimation',
                      overlayAnimationOut: 'customLeaveOverlayAnimation',
                      modalAnimationIn: 'customEnterModalAnimation',
                      modalAnimationOut: 'customLeaveModalAnimation',
                    }}
                    open={this.state.loading} 
                    animationDuration={800}
                    center 
                    onClose={() => this.closeModal()} 
                    >
                        <p>Waiting transaction to be approved</p>
                        <Loader type="Puff" color="#00BFFF" height="100" width="100" />
                        <a href={this.state.transaction} rel="noreferrer" target="_blank">EtherScan transaction</a>
                </Modal>

        <ConnectWalletWrapper>
          <Wrapper>
              <Title>
                "Claim your $PNM tokens accrued by holding a NFT with the $PNM attribut"
              </Title>
              <Spacer/>
          </Wrapper>
          <Spacer/>
            {this.state.tokenRewardsClaimable >0 &&
              <ButtonClaim onClick={this.state.account === '' ? '' : () => this.claimToken()}>{this.state.account === '' ? 'Not connected' : 'Claim $PNM'}</ButtonClaim>
            }
        </ConnectWalletWrapper>

    <ContainerRewardsW>
      <Glow></Glow>
          <RewardsWrapper>
          <ContainerPanels>
            <ContainerCollectedRewards >
                <div>Pending $PNM rewards</div>
                <div><span >{this.state.tokenRewardsClaimable} $PNM</span></div>
            </ContainerCollectedRewards>
            <ContainerCollectedRewards>
                <div>$PNM token balance</div>
                <div><span >{this.state.tokenRewards} $PNM</span></div>
            </ContainerCollectedRewards>
            </ContainerPanels>
          </RewardsWrapper>
        </ContainerRewardsW>
      </AppWrapper>
    );
    }
  }
}


const Glow = styled.div`
position: absolute;
top: 60%;
left: 10%;
z-index: -3;
box-shadow: 110px 50px 130px 60px #fcd63594;

  @media only screen and (min-width: 768px) {
    box-shadow: 150px 70px 170px 70px #fcd63594;
  }
`

const AppWrapper = styled.div`
    padding: 20px;
    display: flex;
    flex-direction: column;
    min-height: 10vh;

`

const Spacer = styled.div`
flex: 1;
`

const ContainerPanels = styled.div`
  position:relative;
  margin-top: 5%;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  text-align: center;

  @media only screen and (min-width: 768px) {
    margin-top: 1px;
  }
`

const RewardsWrapper = styled.div`
  padding: 50px 25px 25px 25px;

  @media only screen and (min-width: 768px) {
    padding: 100px 133px 82px 133px;
  }
`

const ContainerRewardsW = styled.div`
  position: relative;
  width: auto;
  margin: 10px auto 0;

  @media only screen and (min-width: 768px) {
    width: 768px;
  }
`

const ContainerCollectedRewards = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100px;
  width: 200%;

  color: white;
  border: 1px solid #D29EE9;
  background: rgb(252 214 53 / 20%);
  border-radius: 7px;

  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  color: white;
  background-color: transparent;
  letter-spacing: 1px;
  text-transform: uppercase;

  & span {
    color: #fcd635;
    font-size: 6px;
  }

  & div {
    font-size: 6px;
  }


  width: 45%;
  padding: 5px 10px;

  @media only screen and (min-width: 768px) {
    height: 120px;
    width: 100px;
    font-size: 14px;
    width: unset;
    padding: 10px 15px;
    letter-spacing: 2px;
    & span {
        color: #fcd635;
        font-size: 12px;
      }
    & div {
    font-size: 12px;
  }

  }
`

const ConnectWalletWrapper = styled.div`
  text-align: center;
`

const ButtonClaim = styled.button`
  border: 2px solid #D29EE9;
  border-radius: 7px;
  font-size: 8px;
  font-style: normal;
  font-weight: 700;
  line-height: 5px;
  color: white;
  padding: 8px  14px;
  background-color: transparent;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    border: 3px solid #D29EE9;
    outline: 5px solid #fff;
    outline-offset: -5px;
  }

  @media (min-width: 768px) {
    border: 3px solid #D29EE9;
    font-size: 10px;
    padding-left: 2px;
    padding-right: 2px;
    line-height: 19px;
    padding: 10px 18px;
  }
`

const Title = styled.text`
  font-size: 1.2em;
  text-align: center;
  color: white;
  font-size: 11px;
  font-weight: 700;
  background-color: transparent;
  letter-spacing: 1px;
  text-transform: uppercase;
  @media (min-width: 768px) {
    font-size: 18px;
    letter-spacing: 2px;
    padding-left: 2px;
    padding-right: 2px;
  }
`;


// Create a Wrapper component that'll render a <section> tag with some styles
const Wrapper = styled.div`
  padding: 1em;
`;

export default TOKEN;
