import React, { useState, useEffect } from 'react';
import { fabric } from 'fabric';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import {ImagesPool} from './ImagesPool';
import { isMobile } from "react-device-detect";

import background from '../assets/img/canvas.jpeg';

const Canvas = () => {

  const [canvas, setCanvas] = useState('');    
  const [stick, setSticker] = useState('');    
  const [imgURL, setImgURL] = useState('');
  const [originW, setOriginW] = useState(0);
  const [originH, setOriginH] = useState(0);

  const [width, setWidth]   = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);

  const updateDimensions = () => {
    setWidth(window.innerWidth);
    setHeight(window.innerHeight);
  }
  useEffect(() => {
    window.addEventListener("resize", updateDimensions);
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);


  useEffect(() => {
    setCanvas(initCanvas());
  }, []);  


  
  const initCanvas = () => (

    new fabric.Canvas('canvas', {
      height: 0,
      width: 0,
      top: 0,
    })
  );    

    const addImg = (e, canvi) => {
        var reader = new FileReader();
      reader.onload = function(e) {
        var image = new Image();
        image.src = e.target.result;
        image.onload = function() {
          var img = new fabric.Image(image);

          if (img.width < width) {
          setOriginH(image.height)
          setOriginW(image.width)
          canvi.setWidth(image.width)
          canvi.setHeight(image.height)
          }
          else{

          var ratio = img.width/width
          if (ratio > 2)  ratio = 3
          setOriginH(image.height/ ratio)
          setOriginW(image.width/ ratio)
          canvi.setWidth(image.width/ ratio)
          canvi.setHeight(image.height/ ratio)
          }


          canvas.calcOffset()
          img.set({
            left: 0,
            top: 0,
            angle: 0,
            layer: 1,
            scaleX: canvi.width/img.width,
            scaleY: canvi.height/img.height,
          });
          canvi.setBackgroundImage(img);
          canvi.renderAll()
          var zoom = canvi.getZoom()
          console.log(width/img.width, width,img.width)
          if ( img.width >= width) zoom =  width/img.width 
          if ( img.width < width ) zoom = 1
          canvi.setZoom(zoom)
          console.log(img.width,zoom)
        }
      }
      setImgURL(e.target.files[0]["name"])
      console.log(imgURL)
      reader.readAsDataURL(e.target.files[0]);

     }


  const removeRect = (canvi)  => {
      canvi.remove(stick)
      canvi.renderAll()
      setSticker("")
  }



  const addRect = (canvi,sticker)  => {

    if(stick.length === 0 )
    {
        fabric.Image.fromURL(sticker, function (myImg) {
          const img1 = myImg.set({
            left: 0,
            top: 0,
            angle: 0,
            layer: 1,
            scaleX: (canvi.width/myImg.width)*.5,
            scaleY: (canvi.height/myImg.height)*.5,
          })
          setSticker(img1)
          canvi.add(img1)
          canvi.bringToFront(img1)
          canvi.renderAll()
        });

    }

  }



  const downloadImage = canvi => {

    canvi.setZoom(1)

    const ext = "png";
    const base64 = canvi.toDataURL({
      format: ext,
      quality: 1,
      scaleY: originW,
      scaleX: originH
    });
    const link = document.createElement("a");
    link.href = base64;
    link.download = `Sticker_PFP.${ext}`;
    link.click();

  }


  return(
    <Wrapper>
              <Header />
              <InputDiv>
                 <TitleLabel>
                    Put a panty on your profile pic.
                 </TitleLabel>
                 <div>
               <MinInput
                     id="upload-photo"
                     type="file" 
                     onChange={(e) => addImg(e, canvas)}
                     accept="image/png, image/jpeg"
                 />
                <MinLabel>
                    Upload your PFP
                 </MinLabel>
              {isMobile && imgURL.length > 1 &&
                        <Mobile>
                             <br />
                             <ButtonClaimMobile onClick={() => downloadImage(canvas)}>Download</ButtonClaimMobile>
                             <ButtonClaimMobile onClick={() => removeRect(canvas)}>Remove Panty</ButtonClaimMobile>
                             <br />
                       </Mobile>
                   }
             </div>
             </InputDiv>
              <But>
              {imgURL.length > 1 &&
                <>
                    <ScrollLabel>Pick your panty and hat here</ScrollLabel>
                    <ScrollableContainer>
                        <GridContainer>
                            {ImagesPool.map((item) => (
                                <Container >
                                <ImgDiv key={item.src} src={item.src} onClick={() => addRect(canvas,item.src)} /> 
                                </Container>
                                  ))}
                        </GridContainer>
                    </ScrollableContainer>

                    {imgURL.length > 1 && isMobile &&
                        <Mobile>
                             <br />
                            <MoveLabel>Move, rescale and rotate your panty, using your hands</MoveLabel>
                       </Mobile>
                   }

                 </>
                 }
                 <CC>
                            <>
                                {imgURL.length > 1 &&  !isMobile &&
                                <MoveLabel>Move, rescale and rotate your panty, using your hands</MoveLabel>
                                }
                                < br />
                            </>
                         <CanvasDiv id="canvas"  />
                        {imgURL.length > 1 && !isMobile &&
                        <>
                             <ButtonClaim onClick={() => downloadImage(canvas)}>Download</ButtonClaim>
                             <br />
                             <ButtonClaim onClick={() => removeRect(canvas)}>Remove Panty</ButtonClaim>
                       </>
                       }
                </CC>
              </But>
    </Wrapper>
  );
}



